export const GetTranslation =  async (input, sourceLang, targetLang) => {

    const TranslationData = {
        "input": input,
        "sourceLang": sourceLang,
        "targetLang": targetLang
        }

    try {
        const {data} = await ServerApi.post(
            translateURl,
            TranslationData
        )
    return data
    } catch(error) {
            console.error(error);
            return 'Could not translate';

    }

}


